
import {chain,concat,value,forEach,bind,map} from 'lodash-es';

class Animation {
    // this.head, ch tempProgress, max, direction, time, repStyle(once,repeat,bounce)
       constructor(name,animArr,condition) {
        this.name = name;
        this.condition = condition||null;
        this.animArr = animArr;
        this.done = true;
    }
    animate (dt){
        // [this.head, 0, 5, [0,-1], 24, 'bounce',1],
        
        for (let i = 0; i < this.animArr.length; i++) {
            let an= this.animArr[i];
            let time = an[4];
            let max = an[2];
            let part = an[0];
            let normal =  Vector.normal(an[3]);
            let stepVect = Vector.multiply(normal, (max/time)*dt);
            let stepDist = Vector.vectToDist(stepVect);

            switch (an[5]) {
                case 'bounce':
                    if( an[1] >= 0 && an[1] <= max){
                        //Here we are moving body part
                        part.translate(stepVect); 
                        an[1]+=stepDist*an[6];
                    } 
                    if(an[1] < 0){
                        part.moveTo(part.originalAjustPos);
                        an[3] = Vector.reverse(an[3]);  
                        an[1] = 0; an[6]=-an[6];
                    }
                    if(an[1] > max){
                        an[3] = Vector.reverse(an[3]); an[1] = max; an[6]=-an[6];
                    }
                break;

                case 'bounceOnce':
                    if( an[1] >= 0 && an[1] <= max){
                        part.translate(stepVect); an[1]+=stepDist*an[6];
                        this.done = false;
                    }
                    if(an[1] < 0){
                        part.moveTo(part.originalAjustPos);
                        an[3] = Vector.reverse(an[3]);  
                        an[1] = 0; 
                        an[6]=-an[6];
                        this.done = true;
                    }
                    if(an[1] > max){
                        an[3] = Vector.reverse(an[3]); an[1] = max; an[6]=-an[6];
                    }
                break;

                case 'moveOnce':
                if( an[1] >= 0 && an[1] <= max){
                    part.translate(stepVect); an[1]+=stepDist*an[6];
                    this.done = false;
                }
                if(an[1] > max){
                    an[1] = max;
                    this.done = true;
                }
                break;

                case 'move':
                if( an[1] >= 0 && an[1] <= max){
                    part.translate(stepVect); an[1]+=stepDist*an[6];
                    this.done = false;
                }
                if(an[1] > max){
                    an[3] = Vector.reverse(an[3]); an[1] = max; an[6]=-an[6];
                    
                }
                break;
            }
        }
    };
};


class Animator {
    constructor(animList, entryState) {
        this.animList = animList;
        this.animStates = map(animList,el=>el.name);
        this.animEnded = false;
        this.entryState = entryState;
        this.curAnimation = entryState;
        events.on('updateEachFrame', bind(this.animatorLooping,this));
    }
    animatorLooping (e,dt,entities){
        for(let i=0; i<this.animStates.length; i++){
            if (this.curAnimation == this.animStates[i]){
                this.animList[i].animate(dt);
                // console.log('1111');
                if(this.animList[i].done == true){
                    this.curAnimation = this.entryState;
                } 
            } 
        }
    }
}

window.Animation = Animation;
window.Animator = Animator;